import React, { useState } from 'react';
import FsLightbox from 'fslightbox-react';
import '../css/Wqndemo.scss';

import ImageDemo from '../img/image_demo.jpg';

import Wqn2020_1 from '../img/wqn/wqn2020-01.jpg';
import Wqn2020_2 from '../img/wqn/wqn2020-02.jpg';
import Wqn2020_3 from '../img/wqn/wqn2020-03.jpg';

import Wqn2021_1 from '../img/wqn/wqn2021-01.jpg';
import Wqn2021_2 from '../img/wqn/wqn2021-02.jpg';
import Wqn2021_3 from '../img/wqn/wqn2021-03.jpg';
import Wqn2021_4 from '../img/wqn/wqn2021-04.jpg';

export default function Wqndemo() {

    const [lightboxController, setLightboxController] = useState({
        toggler: false,
        slide: 1
    });

    function openLightboxOnSlide(number) {
        setLightboxController({
            toggler: !lightboxController.toggler,
            slide: number
        })
    }

    const navClick = function(e) {
        e.preventDefault();
    };

    return (
        <div className="relative">
            <FsLightbox
                toggler={lightboxController.toggler}
                sources={[
                    Wqn2020_1,
                    Wqn2020_2,
                    Wqn2020_3,
                    Wqn2021_1,
                    Wqn2021_2,
                    Wqn2021_3,
                    Wqn2021_4,
                ]}
                slide={lightboxController.slide}
            />
            <div className="absolute right-0 left-0 top-2/3 h-48 bg-Queen_blue hidden lg:block z-10"></div>
            <div className="max-w-6xl mx-auto flex flex-col md:flex-row gap-8 items-center">
                {/* RWD DEMO */}
                <div className="flex flex-col items-center px-8 py-12 bg-gray-800 rounded-3xl shadow-mobile z-20">
                    <div className="flex items-center mb-2">
                        <div className="flex items-center justify-center w-4 h-4 bg-gray-500 rounded-full">
                            <div className="w-3 h-3 bg-gray-700 rounded-full"></div>
                        </div>
                        <div className="w-48 h-3 ml-2 bg-gray-600 rounded-3xl"></div>
                    </div>
                    <div className="relative bg-white overflow-hidden">
                        <div className="mx-4 flex flex-col items-center">
                            <a href="https://wqn.com.tw/" target="_black">
                                <img src="https://wqn.com.tw/images/wqn_logo.png" alt="https://wqn.com.tw/images/wqn_logo.png" className="w-44 my-4"></img>
                            </a>
                            <img src={ImageDemo} alt={ImageDemo} className="max-w-xs pb-4"></img>
                            <p className="text-2xl pb-4">主要項目</p>
                            <p className="max-w-xs pb-8 text-gray-600">各式品牌書寫用品、黏貼包裝材料、桌上五金用品、檔案收納資料整理、辦公紙品便條紙、圖畫紙工業用紙、事務機器電腦耗材、茶水招待…等等，商品最齊全的文具供應商。</p>
                        </div>
                        <input className="hidden" type="checkbox" id="mobilebar_handle"></input>
                        <div className="absolute pb-16 -ml-56 top-0 bottom-0 w-48 bg-white text-center overflow-scroll duration-500 z-40" id="mobilebar">
                            <p className="py-5 block text-2xl border-b">商品分類</p>
                            <ul>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🖊️#書寫筆</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>📝#修正文具</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🖃#印章墨水</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🔨#桌上五金</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>📦#包裝黏著</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>📎#裝訂事務</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>📏#度量衡</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🧾#紙製品</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🚧#標示貼牌</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>📘#檔案夾</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🗃️#檔案收納</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🪑#辦公傢俱</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>📠#事務機器</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🌲#耗材紙類</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🖱️#電腦耗材</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🖌️#美術用品</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🏟️#運動益智用品</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🛠️#五金百貨</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🧹#清潔用品</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>🥤#辦公茶水</button></li>
                                <li className="p-2 my-1 block border-b"><button onClick={navClick}>✒️#設計印刷</button></li>
                            </ul>
                        </div>
                        <nav className="flex border justify-center">
                            <p className="p-5 text-gray-300">首頁</p>
                            <p className="p-5 text-gray-300">商品</p>
                            <p className="p-5 text-gray-300">型錄</p>
                            <label className="p-5 cursor-pointer animate-bounce" htmlFor="mobilebar_handle">分類</label>
                        </nav>
                    </div>
                </div>
                {/* RWD DEMO END */}
                <div className="mx-3 md:px-0 z-20 timeline">
                    <div className="mb-8 pl-12 timepoint">
                        <p className="text-2xl">2019 - 2020</p>
                        <p className="py-3">
                            使用 Nuxt.js 製作的靜態企業網站<br/>
                            提供 SEO 優化及良好用戶體驗<br/>
                            標準化資料夾結構，後續要修改也能簡單明瞭<br/>
                            利用 LINE Messaging API 製作簡易商品介紹聊天機器人 ( Node.js )
                        </p>
                        <div className="flex grid grid-cols-2 md:grid-cols-4 gap-4">
                            <img src={Wqn2020_1} alt={Wqn2020_1} className="w-auto shadow-xl hover:animate-weak-bounce" onClick={()=>{openLightboxOnSlide(1)}}></img>
                            <img src={Wqn2020_2} alt={Wqn2020_2} className="w-auto shadow-xl hover:animate-weak-bounce" onClick={()=>{openLightboxOnSlide(2)}}></img>
                            <img src={Wqn2020_3} alt={Wqn2020_3} className="h-32 shadow-xl hover:animate-weak-bounce" onClick={()=>{openLightboxOnSlide(3)}}></img>
                        </div>
                    </div>
                    <div className="mb-8 pl-12 timepoint">
                        <p className="text-2xl">2021</p>
                        <p className="py-3">
                            使用 Laravel + Livewire <br/>
                            快速建立 CRUD 功能<br/>
                            提供客戶更完善資料查詢功能<br/>
                            <br/>
                        </p>
                        <div className="flex grid grid-cols-2 md:grid-cols-4 gap-4">
                            <img src={Wqn2021_1} alt={Wqn2021_1} className="w-auto shadow-xl hover:animate-weak-bounce" onClick={()=>{openLightboxOnSlide(3)}}></img>
                            <img src={Wqn2021_2} alt={Wqn2021_2} className="w-auto shadow-xl hover:animate-weak-bounce" onClick={()=>{openLightboxOnSlide(4)}}></img>
                            <img src={Wqn2021_3} alt={Wqn2021_3} className="w-auto shadow-xl hover:animate-weak-bounce" onClick={()=>{openLightboxOnSlide(5)}}></img>
                            <img src={Wqn2021_4} alt={Wqn2021_4} className="w-auto shadow-xl hover:animate-weak-bounce" onClick={()=>{openLightboxOnSlide(6)}}></img>
                        </div>
                        
                    </div>
                </div>
            </div>
        </div>
    )
};
