import React, { useState } from 'react';
import FsLightbox from 'fslightbox-react';

import Myworks_1 from '../img/myworks/myworks-01.jpg';
import Myworks_2 from '../img/myworks/myworks-02.jpg';
import Myworks_3 from '../img/myworks/myworks-03.jpg';
import Myworks_4 from '../img/myworks/myworks-04.jpg';
import Myworks_5 from '../img/myworks/myworks-05.jpg';
import Myworks_6 from '../img/myworks/myworks-06.jpg';
import Myworks_7 from '../img/myworks/myworks-07.jpg';
import Myworks_8 from '../img/myworks/myworks-08.jpg';

export default function Myworks() {

    const [lightboxController, setLightboxController] = useState({
        toggler: false,
        slide: 1
    });

    function openLightboxOnSlide(number) {
        setLightboxController({
            toggler: !lightboxController.toggler,
            slide: number
        })
    }

    return (
        <div>
            <FsLightbox
                toggler={lightboxController.toggler}
                sources={[
                    Myworks_1,
                    Myworks_2,
                    Myworks_3,
                    Myworks_4,
                    Myworks_5,
                    Myworks_6,
                    Myworks_7,
                    Myworks_8
                ]}
                slide={lightboxController.slide}
            />
            <div className="flex justify-between grid grid-cols-3 lg:grid-cols-6 gap-8">
                <div >
                    <img src={Myworks_1} alt={Myworks_1} className="shadow-xl rounded border-4 border-Queen_blue" onClick={()=>{openLightboxOnSlide(1)}}></img>
                </div>
                <div >
                    <img src={Myworks_2} alt={Myworks_2} className="shadow-xl rounded border-4 border-Queen_blue" onClick={()=>{openLightboxOnSlide(2)}}></img>
                </div>
                <div >
                    <img src={Myworks_3} alt={Myworks_3} className="shadow-xl rounded border-4 border-Queen_blue" onClick={()=>{openLightboxOnSlide(3)}}></img>
                </div>
                <div >
                    <img src={Myworks_4} alt={Myworks_4} className="shadow-xl rounded border-4 border-Queen_blue" onClick={()=>{openLightboxOnSlide(4)}}></img>
                </div>
                <div >
                    <img src={Myworks_5} alt={Myworks_5} className="shadow-xl rounded border-4 border-Queen_blue" onClick={()=>{openLightboxOnSlide(5)}}></img>
                </div>
                <div >
                    <img src={Myworks_6} alt={Myworks_6} className="shadow-xl rounded border-4 border-Queen_blue" onClick={()=>{openLightboxOnSlide(6)}}></img>
                </div>
                <div >
                    <img src={Myworks_7} alt={Myworks_7} className="shadow-xl rounded border-4 border-Queen_blue" onClick={()=>{openLightboxOnSlide(7)}}></img>
                </div>
                <div >
                    <img src={Myworks_8} alt={Myworks_8} className="shadow-xl rounded border-4 border-Queen_blue" onClick={()=>{openLightboxOnSlide(8)}}></img>
                </div>
            </div>
        </div>
    )
};

