import React from 'react';
import coding from '../img/myweb_coding.png';
import design from '../img/myweb_design.png';
import skill from '../img/myweb_skill.png';
import speak from '../img/myweb_speak.png';

class About extends React.Component {
    render() {
        return (
            <div className="max-w-6xl mx-auto flex grid grid-cols-2 md:grid-cols-4 gap-8 py-6 px-3 lg:px-0 text-gray-200">
                <div className="text-center">
                    <img src={coding} alt="coding" className="mb-3"/>
                    <div className="p-3">
                        <h3 className="font-bold text-xl">網站設計專長</h3>
                        <p>專精於網頁設計，RWD響應式網頁，了解網頁規劃流程</p>
                    </div>
                </div>
                <div className="text-center">
                    <img src={design} alt="design" className="mb-3"/>
                    <div className="p-3">
                        <h3 className="font-bold text-xl">平面視覺設計</h3>
                        <p>多年的設計經驗，客製化標示，有效處理客戶要求</p>
                    </div>
                </div>
                <div className="text-center">
                    <img src={skill} alt="skill" className="mb-3"/>
                    <div className="p-3">
                        <h3 className="font-bold text-xl">不段充實自己</h3>
                        <p>具學習新技術的熱誠，不斷追求新知識</p>
                    </div>
                </div>
                <div className="text-center">
                    <img src={speak} alt="speak" className="mb-3"/>
                    <div className="p-3">
                        <h3 className="font-bold text-xl">清楚表達想法</h3>
                        <p>善於溝通，勇於表達想法</p>
                    </div>
                </div>
            </div>
        )
    }
}

export default About;
